import { Route } from 'react-router-dom'
import './App.css'
import V2Docs from './Comps/Version2/V2Docs'
import V2Home from './Comps/Version2/V2Home'
import V2About from './Comps/Version2/V2About'
import V2Nav from './Comps/Version2/Nav/V2Nav'
import VersionNav from './VersionNav'
import V1Home from './Comps/Version1/V1Home'
import V1Nav from './Comps/Version1/V1Nav'
import V1About from './Comps/Version1/V1About'
import V1Docs from './Comps/Version1/V1Docs'

function App () {
	return (
		<div className={'App'}>
			<VersionNav/>
			<a href={'https://austimcoin.webflow.io/'}>https://austimcoin.webflow.io/</a>
			<Route path={'/v1'} render={() => <V1Nav/>}/>
			<Route exact path={'/v1'} render={() => <V1Home/>}/>
			<Route exact path={'/v1/about'} render={(props) => <V1About {...props}/>}/>
			<Route exact path={'/v1/docs'} render={(props) => <V1Docs {...props}/>}/>
			
			<Route path={'/v2'} render={() => <V2Nav/>}/>
			<Route exact path={'/v2'} render={() => <V2Home/>}/>
			<Route exact path={'/v2/about'} render={(props) => <V2About {...props}/>}/>
			<Route exact path={'/v2/docs'} render={(props) => <V2Docs {...props}/>}/>
			
		</div>
	)
}

export default App
