import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import '../../Nav.css'

class V1Nav extends Component {
	constructor (props) {
		super(props)
		this.state = {
			isVis: false
		}
		this.navToggle = this.navToggle.bind(this)
	}
	navToggle (e) {
		e.preventDefault()
		this.setState((St) => ({ isVis: !St.isVis }))
	}
	render () {
		const { isVis } = this.state
		let swapLink = 'https://exchange.pancakeswap.finance/#/swap?outputCurrency='
		return (
			<div id={'navbar'}>
				<NavLink exact className={'navLink'} activeClassName={'nactive nlogo'} to={'/v1'}>LOGO(Home)</NavLink>
				<a target={'_blank'} rel={'noreferrer'} className={'navLink'} href={swapLink}>Buy</a>
				<NavLink className={'navLink'} activeClassName={'nactive'} to={'/v1/about'}>About</NavLink>
				<NavLink className={'navLink'} activeClassName={'nactive'} to={'/v1/docs'}>Docs</NavLink>
				<div id={'nav-icon'} className={`${isVis ? 'open' : ''}`} onClick={this.navToggle}>
					<span></span>
					<span></span>
					<span></span>
					<span></span>
				</div>
			</div>
		)
	}
}

export default V1Nav
